import { useState } from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import './App.css';
import About from './components/About';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import Alert from './components/Alert';
// import { BrowserRouter as Router,
//          Routes,
//          Route,
//          Link
//          } from "react-router-dom";

function App() {

  const [mode, setMode] = useState('light');
  const [alert, setAlert] = useState(null);
  
  const toggleMode = ()=>{
  if (mode === 'light'){
     setMode('dark');
     document.body.style.backgroundColor = 'rgba(var(--bs-dark-rgb))';
     setAlert("Success: Dark Mode Has Been Enabled");
      setTimeout(()=>{
       setAlert(null);
      },1500);
      // document.title = "TextUtils - dark mode";
  }
  else{
  setMode('light');
  document.body.style.backgroundColor = 'white';
  setAlert("Success: Light Mode Has Been Enabled");
  }
} 

  return (
    <BrowserRouter>

   {/* <Navbar/> */}
   <Navbar title="TextUtils" aboutText="About TextUtils" mode={mode} toggleMode = {toggleMode} />
   {/* <Navbar title='TextUtils'/> */}

   <Alert alert = {alert} mode = {mode} toggleMode = {toggleMode}/>

   <div className = "container my-4">
    <Routes>

      <Route  exact path="about"   
       element = {<About/>}>
      </Route>
      
      <Route exact path="/"
       element = {<TextForm heading = "Enter the Text to Analysis" mode = {mode} toggleMode = {toggleMode} />}>
      </Route>

    </Routes>

   {/* <TextForm heading = "Enter the Text to Analysis" mode = {mode} toggleMode = {toggleMode} /> */}
   {/* <TextForm/> */}

   {/* <About/> */}
   
   </div>

   </BrowserRouter>
  );
}

export default App;
