import { clear } from "@testing-library/user-event/dist/clear";
import React , {useState} from "react";

export default function TextForm(props) {
  const handleUpClick = ()=>{
    console.log("uppercase was clicked - " + text);
    let newText = text.toUpperCase();
    setText(newText);
  }
  const handleLoClick = ()=>{
    console.log("lowercase was clicked - " + text);
    let newText = text.toLowerCase();
    setText(newText);
  }
  const handleToggle = ()=>{
    setShow(!show);
  }
  const handleOnChange = (event)=>{
    console.log("on change");
    setText(event.target.value);
  }
  const handleClear = ()=>{
    let newText = "";
    setText(newText);
  }
  const handleCopy = ()=>{
    var text = document.getElementById('myBox');
    text.select();
    navigator.clipboard.writeText(text.value);
  }
  const handleExtraSpaces = ()=>{
   let newText = text.split(/[ ]+/); // regex concept.
   setText(newText.join(" "))
  }
  const [text, setText] = useState('');
  const [show, setShow] = useState(true);

  return (
    <>
    <div className="container" style={{ color: props.mode === "dark"? "white":"black"}} >
     <h1> {props.heading}</h1> 
     <div className="mb-3">
     <textarea className="form-control" value={text} onChange={handleOnChange} style={{backgroundColor: props.mode === "dark"? "grey":"white",
      color: props.mode === "dark"? "white":"black"
     }} id="myBox" rows="8">      
      </textarea>
     </div>  
    </div>
    <div style={{ color: props.mode === "dark"? "white":"black"}}>
      {
        show?<h1>preview</h1>:null}
        {
        show?<p>{text}</p>:null
      }
      </div>
      <button className="btnbtnprimary mx-2" onClick={handleUpClick}> convert to uppercase</button>

      <button className="btnbtnprimary mx-2" onClick={handleLoClick}> convert to lowercase</button>

      <button className="btnbtnprimary mx-2" onClick={handleClear}> clear </button>

      <button className="btnbtnprimary mx-2" onClick={handleToggle}> toggle </button>

      <button className="btnbtnprimary mx-2" onClick={handleCopy}> Copy </button>

      <button className="btnbtnprimary mx-2" onClick={handleExtraSpaces}> handle Extra Spaces </button> 

    <div className="container2 my-3" style={{ color: props.mode === "dark"? "white":"black",
     border:  props.mode === "dark" ? "solid white 3px":"solid black 3px"
  }}>
      <h1><b><u>your Text summary</u></b></h1>
      <p>{text.length} Characters</p>
      <p>{(text.split(" ").length)} Words </p>
      <p>{0.008 * text.split(" ").length} min to read</p>
    </div>
    </>
  );
}
